<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
  <div class="container-fluid">
    <router-link class="navbar-brand" to='/'>前台</router-link>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav">
        <li class="nav-item">
          <router-link class="nav-link" to="/products">產品列表</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/cart">購物車</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/signin">登入</router-link>
        </li>
      </ul>
    </div>
    <button type='button' class='btn btn-primary' :disabled="cartData?.carts?.length===0">結帳
      <span class="badge rounded-pill bg-danger bg-light text-dark" v-if="cartData?.carts?.length>0">{{cartData?.carts?.length}}</span>
    </button>
  </div>
</nav>
</template>
<script>
import emitter from '@/libs/emitter'
export default {
  data () {
    return {
      cartData: {},
      cart: {
        carts: []
      }
    }
  },
  methods: {
    renderCart () {
      const url = `${process.env.VUE_APP_API}/v2/api/${process.env.VUE_APP_PATH}/cart`
      this.$http(url)
        .then((res) => {
          this.cartData = res.data.data
          console.log('cartData', this.cartData)
          if (this.cartData.carts.length === 0) {
            this.cart = {
              carts: []
            }
          } else {
            this.cart = {
              carts: [1]
            }
          }
        })
        .catch((err) => {
          console.log(err)
        })
    }
  },
  mounted () {
    this.renderCart()
    emitter.on('render-cart', () => {
      this.renderCart()
    })
  }
}
</script>
